<template>
  <div id="login" class="grey lighten-5">
    <v-container class="pt-5">
      <v-layout wrap class="mt-5">
        <v-flex sm12 md6 offset-md3>
          <v-card elevation="4" light tag="section">
            <v-card-title class="red white--text font-weight-bold">
              <v-layout align-center justify-space-between>
                <h3 class="headline">Login</h3>
              </v-layout>
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text>
              <p>Sign in with your username and password:</p>
              <v-form>
                <v-text-field
                  outline
                  label="Email Address"
                  type="email"
                  v-model="email"
                  required
                ></v-text-field>
                <v-text-field
                  outline
                  hide-details
                  label="Password"
                  v-model="password"
                  :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="show ? 'text' : 'password'"
                  class="input-group--focused"
                  @click:append="show = !show"
                  required
                ></v-text-field>
              </v-form>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions :class="{ 'pa-3': $vuetify.breakpoint.smAndUp }">
              <v-btn class="font-weight-bold" color="error" text>
                Forgot password?
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn
                class="font-weight-bold"
                @click="login"
                color="error"
                :large="$vuetify.breakpoint.smAndUp"
              >
                <v-icon left>mdi-lock</v-icon>
                Login
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
        <v-flex sm12 md6 offset-md3>
          <v-layout align-center justify-space-between>
            <p class="caption my-3">
              <a href="https://github.com/conceptcodes" class="red--text font-weight-bold">Privacy Policy</a>
              |
              <a href="https://github.com/conceptcodes" class="red--text font-weight-bold">Terms of Service</a>
            </p>

            <p class="caption my-3">
              Powered by
              <a
                class="red--text font-weight-bold"
                href="https://github.com/conceptcodes"
                >Cafe Bustello</a
              >
            </p>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "login",
  data: () => ({
    email: null,
    password: null,
    show: false,
    timeout: 3000,
  }),
  methods: {
    ...mapActions(["loginUser"]),
    login() {
      this.alert = false;
      this.loginUser({ email: this.email, password: this.password })
        .then(() => { 
          window.alert('Success')
          this.$router.push({ name: "home" }) 
        })
        .catch((err) => { 
          console.error(err)
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.v-btn,
.v-card {
  border-radius: 4px;
}
.v-card__title {
  text-transform: uppercase;
}
a {
  text-decoration: none;
}
#login {
  height: 100%;
}
</style>